import React from 'react'

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'))

/** Administrateurs */
const Administrateurs = React.lazy(() => import('./views/administrateurs/Administrateurs'))
const DetailAdministrateur = React.lazy(() => import('./views/administrateurs/DetailAdministrateur'))
const AjoutAdministrateur = React.lazy(() => import('./views/administrateurs/AjoutAdministrateur'))

/** CLients */
const Clients = React.lazy(() => import('./views/clients/Clients'))
const DetailClient = React.lazy(() => import('./views/clients/DetailClient'))

/** Commandes */
const Commandes = React.lazy(() => import('./views/commandes/Commandes'))
const DetailCommande = React.lazy(() => import('./views/commandes/DetailCommande'))

/** Commercants */
const Commercants = React.lazy(() => import('./views/commercants/Commercants'))
const DetailCommercant = React.lazy(() => import('./views/commercants/DetailCommercant'))
const AjoutCommercant = React.lazy(() => import('./views/commercants/AjoutCommercant'))

/** Restaurants */
const Restaurants = React.lazy(() => import('./views/restaurants/Restaurants'))

const Paiements = React.lazy(() => import('./views/paiements/Paiements'))

const routes = [
    { path: '/', exact: true, name: 'Home' },
    { path: '/dashboard', name: 'Dashboard', component: Dashboard },
    { path: '/paiements', name: 'Paiements', component: Paiements },

    /** Routes Administrateurs */
    { path: '/administrateurs', name: 'Administrateurs', component: Administrateurs },
    { path: '/detail-admin/:id', name: 'DetailAdministrateur', component: DetailAdministrateur },
    { path: '/ajout-admin/:id?', name: 'AjoutAdministrateur', component: AjoutAdministrateur },

    /** Routes Clients */
    { path: '/clients', name: 'Clients', component: Clients },
    { path: '/detail-client/:id', name: 'DetailClient', component: DetailClient },

    /** Routes Commandes */
    { path: '/commandes', name: 'Commandes', component: Commandes },
    { path: '/detail-commande/:id', name: 'DetailCommande', component: DetailCommande },

    /** Routes Restaurants */
    { path: '/restaurants', name: 'Restaurants', component: Restaurants },

    /** Routes Commercants */
    { path: '/commercants', name: 'Commercants', component: Commercants },
    { path: '/detail-commercant/:id', name: 'DetailCommercant', component: DetailCommercant },
    { path: '/ajout-commercant/:id?', name: 'AjoutCommercant', component: AjoutCommercant },
]

export default routes